.header {
    display: flex;
    background-color: #fff;
    padding: 10px 10%;
    justify-content: space-between;
    height: 35px;
    position: sticky;
    top: 0;
    margin-bottom: 20px;
    z-index: 99;
}

.header__left {
    display: flex;

}

.header__left img {
    height: 30px;
    margin-right: 10px;
}

.header__search {    
    display: flex;
    align-items: center;
    background-color: #eef3f8;
    padding: 3px 10px;
    border-radius: 5px;

}

.header__search input {    
    background-color: transparent;
    border: 0px;    
    outline: 0px;
    min-width: 220px;
}

.header__search svg {
    font-size: 20px;
    color: #333;
}

.header__right {
    display: flex;
    align-items: center;
}

.header__options {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0px 15px;
    font-size: 14px;
    color: #565656;
    position: relative;
    cursor: pointer;
}

.header__options:first-child {
    color: #000;
}

.header__options:first-child::before{
    content: "";
    position: absolute;
    left: -10px;
    right: -10px;
    background-color: #333;
    height: 1px;
    bottom: -7px;
}

.header__options .MuiAvatar-circular {
    width: 30px;
    height: 30px;
    object-fit: contain;
}




